import { useEffect, useState } from 'react';
import { Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import { createNote } from './../store.js';
import Radio from './../components/Radio.js'

function CreateNote() {
    let state = useSelector((state) => {return state});
    let note = state.note;
    let dispatch = useDispatch();
    // console.log(note);

    let [titleinputs, setTitle] = useState('');
    let [continputs, setContent] = useState('');
    let uuid = crypto.randomUUID();
    let [priority, setPriority] = useState('Low');
    let [variant, setVariant] = useState('warning');
    let priorityChk = (param) => {setPriority(param);}
    let variantChk = (param) => {setVariant(param)};
    // parm: 자식인 Radio 컴포넌트에서 받은 값

    let newNote = {
        id : uuid,
        title : titleinputs,
        priority : priority,
        variant: variant,
        content : continputs,
        update: moment().format('YYYY-MM-DD HH:mm:ss')
    };
    
    useEffect(() => {
        console.log(newNote);
    }, [titleinputs, continputs, priority])

    return (
        <div className="container">
            <input type="text"
                className="input__title"
                placeholder="Enter title here"
                onChange={(e)=>{
                    setTitle(e.target.value);
                }}/>{' '}

            <Radio priorityChk={priorityChk} variantChk={variantChk}/>
            
            <input type="textarea"
                className='input__content'
                placeholder="Enter detail here"
                onChange={(e)=>{
                    setContent(e.target.value);}}
            />{' '}
            <Button variant="outline-primary"
                onClick={ ()=> {
                    dispatch(createNote(newNote))
                }}>생성</Button>
        </div>
    )
}

export default CreateNote;