import { Card, Badge } from 'react-bootstrap';

function Note(props) {

    return (
        <Card className="note-container__card">
            <Card.Header className="card-header">
                <div className="card-header__heading">
                    {props.note.title}{' '}
                    <span class={`badge badge-${props.note.variant} pill`}>{props.note.priority}</span>{' '}
                </div>
                <span className="card-header__delete">삭제</span>
            </Card.Header>
            <Card.Body>
                <blockquote className="blockquote mb-0">
                <p>
                    {props.note.content}{' '}
                </p>
                <footer className="blockquote-footer">
                    <cite title="Source Title">n분 전</cite>
                </footer>
                </blockquote>
            </Card.Body>
        </Card>
    )
}
export default Note;